@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 35px; }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

@keyframes pulse {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(0.95); }
  100% {
    transform: scale(1); } }

@keyframes fade-from-left {
  from {
    transform: translateX(-100vw); }
  to {
    transform: translateX(0); } }

@keyframes fade-from-top {
  from {
    transform: translateY(-100vh); }
  to {
    transform: translateY(0); } }

@keyframes fade-from-right {
  from {
    transform: translateX(100vw); }
  to {
    transform: translateX(0); } }

.page {
  display: grid;
  min-height: 100%;
  grid-template-columns: minmax(320px, 100%);
  grid-template-rows: min-content 1fr; }

.header {
  position: relative;
  width: 100%;
  min-height: 74px;
  display: flex;
  flex-direction: column;
  padding-top: 25px;
  padding-bottom: 25px; }

.header__wrapper {
  width: 100%;
  display: flex;
  margin-left: auto;
  margin-right: auto; }

.logo {
  position: absolute;
  top: 25px;
  left: 35px;
  line-height: 0;
  transition: all 0.8s ease;
  transform: none;
  z-index: 2; }
  .logo--active {
    top: 50vh;
    left: 50%;
    width: 800px;
    height: 100px;
    transform: translate(-50%, -50%);
    transition: none; }
    @media (max-width: 1023px) {
      .logo--active {
        width: 550px;
        height: 70px; } }

.logo--active svg {
  overflow: visible;
  width: 100%;
  height: 100%; }

.logo--active .logo__text {
  animation: 1.5s fade-from-right; }

.logo--active .logo__main {
  animation: 1s fade-from-top; }

.logo--active .logo__name {
  animation: 1.5s fade-from-left; }

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background-color: #ffffff;
  padding-bottom: 100px;
  z-index: 1; }
  .loader--inactive {
    opacity: 0;
    transition: opacity 0.3s ease; }
  .loader--hidden {
    display: none; }

@media (max-width: 1023px) {
  .loader__desktop {
    display: none; } }

.loader__tablet {
  display: none; }
  @media (max-width: 1023px) {
    .loader__tablet {
      display: inline; } }

.loader span {
  font-size: 24px;
  line-height: 22px; }

.loader__desktop,
.loader__tablet {
  animation: pulse;
  animation-duration: 3s;
  animation-delay: 2s;
  animation-iteration-count: infinite; }

.lead {
  width: 100%;
  min-height: 685px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  background-image: url("../img/bg/bg-main-desktop.svg");
  background-repeat: no-repeat;
  background-position: bottom center;
  padding-left: 35px;
  padding-right: 35px;
  transition: none; }
  @media (max-width: 1023px) {
    .lead {
      flex-direction: row-reverse;
      justify-content: flex-start;
      background-image: none;
      flex-wrap: nowrap;
      transition: transform 0.8s ease;
      padding-left: 0;
      padding-right: 0; } }

.lead__title {
  width: 562px;
  padding-top: 118px;
  margin-right: 40px;
  transform: translateX(-100vw);
  transition: none; }
  @media (max-width: 1023px) {
    .lead__title {
      width: 100%;
      min-width: 100%;
      min-height: 776px;
      padding: 246px 35px 40px;
      margin-right: 0;
      transform: translateX(100vw); } }

.lead__title > h1 {
  width: 562px;
  font-weight: 400;
  font-size: 96px;
  line-height: 96px;
  color: #2c39f2;
  margin: 0; }

.lead__inner {
  width: 352px;
  padding-top: 46px;
  transform: translateX(100vw); }
  @media (max-width: 1023px) {
    .lead__inner {
      width: 100%;
      min-width: 100%;
      min-height: 950px;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: #2c39f2;
      padding-top: 161px;
      padding-bottom: 40px;
      transform: translateX(-100vw); } }

.lead__inner > svg {
  margin-bottom: 28px; }
  @media (max-width: 1023px) {
    .lead__inner > svg {
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 36px; } }

.lead__inner > p {
  font-size: 30px;
  line-height: 40px;
  margin: 0; }
  @media (max-width: 1023px) {
    .lead__inner > p {
      width: 352px;
      color: #ffffff; } }

.lead--loaded > .lead__title,
.lead--loaded > .lead__inner {
  transform: translateX(0);
  transition: transform 0.8s ease;
  transition-duration: 0.8s; }

@media (max-width: 1023px) {
  .lead__lightnings path {
    fill: #ffffff; } }

@media (max-width: 1023px) {
  .lead__stroke {
    stroke: #000000; } }

@media (max-width: 1023px) {
  .lead--clicked {
    transform: translateX(100%);
    transition: transform 0.8s ease; } }

@media (max-width: 1023px) {
  .lead--nojs {
    flex-direction: column; } }

